

































































































import { Component, Vue } from 'vue-property-decorator';
import { DataTableHeader, SelectItem, TableMemberVO } from '@/common/types';
import { getCodeListApi, getListApi } from '@/utils/apis';
import envs from '@/constants/envs';
import ButtonSet from '@/components/speeddial/ButtonSet.vue';
import DataTableFilter from '@/components/datatable/DataTableFilter.vue';
import MemberEditDialog from '@/views/admin/member/components/MemberEditDialog.vue';
import dayjs from 'dayjs';

@Component({
  name: 'MemberList',
  components: {
    MemberEditDialog,
    DataTableFilter,
    ButtonSet,
  },
})
export default class extends Vue {
  readonly envs: typeof envs = envs;
  readonly dayjs: typeof dayjs = dayjs;
  mode: string | null = null;
  sortBy: string[] = ['authority'];
  sortDesc: boolean[] = [true];
  items: TableMemberVO[] = [];
  filteredItems: TableMemberVO[] = [];
  editItem: TableMemberVO = {};
  selected: TableMemberVO[] = [];
  loading: boolean = false;
  dialog: boolean = false;

  AUTHORITY: SelectItem[] | null = null;

  headers: DataTableHeader[] = [
    {
      text: `사용자아이디`,
      align: `start`,
      value: `id`,
    },
    {
      text: `사용자명`,
      align: `start`,
      value: `name`,
    },
    {
      text: `권한`,
      align: `center`,
      value: `authority`,
      filterType: 'select',
      filterSelectItem: [],
    },
    {
      text: `만료일`,
      align: `center`,
      value: `expired`,
      width: 160,
    },
    {
      text: `사용 가능`,
      align: `center`,
      value: `available`,
      filterType: 'switch',
      width: 100,
    },
    {
      text: `자동로그아웃시간(초)`,
      align: `end`,
      value: `timeout`,
      width: 170,
    },
    {
      text: `작업 일시`,
      align: `center`,
      value: `updated`,
      filterable: false,
      width: 160,
    },
    {
      text: `작업자`,
      align: `start`,
      value: `updatedBy`,
      filterable: false,
      width: 100,
    },
  ];

  async mounted() {
    this.headers[2].filterSelectItem = this.AUTHORITY = await getCodeListApi(
      'AUTHORITY',
    );
    await this.getList();
  }

  async getList() {
    this.selected = [];
    this.items = [];
    this.loading = true;
    const response = await getListApi<TableMemberVO[]>(`admin/members/`);
    this.loading = false;
    this.items = response.data || [];
  }
}
